.main {
    background-color: #2E86C1;
}
.wrapper {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
span {
    font-size: 40px;
}
.textBlock {

}
.textBlock p {
    font-size: 50px;
}
.photoBlock {
    height: 450px;
    width: 350px;
    border-radius: 15px;
}
.photoBlock img {
    height: 450px;
    width: 350px;
    border-radius: 15px;
}
@media (max-width: 1024px) {
    .wrapper {
        flex-direction: column;
    }
    .textBlock {
        text-align: center;
        margin-top: 90px;
    }
    .textBlock p {
        font-size: 30px;
        margin: 20px;
    }
    .photoBlock {
        margin-bottom: 40px;
    }
}
@media (max-width: 428px) {
    .main {
        padding-bottom: 30px;
    }
    .photoBlock {
        height: 400px;
        width: 300px;
    }
    .photoBlock img {
        height: 400px;
        width: 300px;
        border-radius: 15px;
    }
}
