.nav {
    padding: 25px;
}
.navItem {
    margin-right: 25px;
    text-decoration: none;
    color: #fff;
    position: relative;
    padding-bottom: 3px;
    cursor: pointer;
}
.navItem:after {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    background-color: #fff;
    transition: all ease .3s;
    bottom: 0;
    left: 0;
    border-radius: 5px;

}
.navItem:hover:after {
    width: 100%;
    transition: all ease .3s;
}
.navItem:last-child {
    margin-right: 0;
}
.active {
    color: black;
}
@media (max-width: 428px) {
    .navItem {
        margin-right: 15px;
    }
}
@media (max-width: 388px) {
    .nav {
        padding-right: 10px;
        padding-left: 10px;
    }
}