.contacts {
    background-color: #2E86C1;
    padding-bottom: 100px;
}
.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contactsWrapper {
    display: flex;
    margin-top: 20px;
    margin-bottom: 50px;
}
.roundButton {
    margin-top: 30px;
    width: 65px;
    height: 65px;
    border-radius: 50%;
    border: 2px solid black;
    margin-right: 50px;
    display: flex;
    align-items: center;
    position: relative;
    transition: all .5s;
}
.roundButton:last-child {
    margin-right: 0;
}
.roundButton:hover {
    transition: all .5s;
    border: 2px solid #fff;
}
.button {
    padding: 10px 25px;
    background-color: #D35400;
    border-radius: 5px;
    color: black;
    text-decoration: none;
    transition: all .4s ease;
}
.button:hover {
    transition: all .4s;
    background-color: powderblue;
}
.buttonImg {
    width: 40px;
    height: 40px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}
.p {
    font-size: 35px;
}
.contact {
    margin-top: 50px;
}
.cont {
    text-decoration: none;
    color: black;
    position: relative;
    padding-bottom: 5px;
}
.cont:after {
    content: '';
    position: absolute;
    width: 0;
    background-color: white;
    height: 2px;
    left: 0;
    bottom: 0;
    border-radius: 10px;
    transition: all ease .3s;
}
.cont:hover:after {
    width: 100%;
    transition: all ease .3s;
}