.work {
    background-color: #2E86C1;
    padding-bottom: 100px;
}
.workTitle {
    text-align: center;
}
.wrapper {
    display: flex;
    justify-content: space-between;
}
.container {
    width: 825px;
    margin: 0 auto;
}
.ulll {
    margin: 0;
}
@media (max-width: 1024px) {
    .container {
        max-width: 600px;
        padding-left: 50px;
        padding-right: 50px;
    }
}
@media (max-width: 768px) {
    .container {
        max-width: 500px;
        padding-left: 30px;
        padding-right: 30px;
    }
}
@media (max-width: 560px) {
    .container {
        max-width: 350px;
        padding-left: 20px;
        padding-right: 20px;
    }
}
@media (max-width: 428px) {
    .container {
        max-width: 300px;
        padding-left: 10px;
        padding-right: 10px;
    }
}
