.container {
    max-width: 1200px;
    margin: 0 auto;
}
.title {
    font-family: 'Lobster', cursive;
    font-size: 45px;
    font-weight: bold;
    text-align: center;
    position: relative;
    padding-bottom: 30px;
    padding-top: 100px;
    margin: 0;
}
.title:after {
    position: absolute;
    content: '';
    width: 30px;
    height: 5px;
    background-color: black;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}
@media (max-width: 1199px) {
    .container {
        padding-right: 50px;
        padding-left: 50px;
    }
}
@media (max-width: 428px) {
    .container {
        padding-right: 20px;
        padding-left: 20px;
    }
}