.projectsItem {
    width: 450px;
}
.imgWrapper  {
    position: relative;
    display: flex;
    align-items: center;
    border-radius: 15px;
}
.projectImg {
    width: 450px;
    border-radius: 15px;
}
.button {
    position: absolute;
    padding: 10px 25px;
    background-color: #D35400;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 5px;
    color: black;
    text-decoration: none;
    transition: all .4s ease;
}
.button:hover {
    transition: all .4s;
    background-color: powderblue;
}
@media (max-width: 1024px) {
    .projectImg {
        margin-top: 40px;
    }
    .projectsItem:last-child {
        margin-bottom: 40px;
    }
}
@media (max-width: 768px) {
    .projectsItem {
        width: 350px;
    }
    .projectImg {
        width: 350px;
    }
}
@media (max-width: 428px) {
    .projectsItem {
        width: 300px;
    }
    .projectImg {
        width: 300px;
    }
}