.skillsItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 300px;

}
.square {
    width: 100px;
    height: 100px;
    background-color: pink;
    border-radius: 10px;
    transform: rotate(46deg);
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 40px;

}
.pic {
    width: 50px;
    height: 50px;
    transform: rotate(-46deg);
}
@media (max-width: 1024px) {
    .square {
        margin-top: 50px;
    }
}